@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@1,500;1,800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Kanit:ital,wght@1,600&display=swap");

/* RESETS */
*,
*::before,
*::after {
  box-sizing: border-box;
}
*:focus {
  outline: 3px dashed #228bec;
  outline-offset: 0;
}
html {
  font: 62.5% / 1.15 sans-serif;
}
h1,
h2 {
  margin-bottom: 0;
}
ul {
  list-style: none;
  padding: 0;
}
button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  appearance: none;
}
button::-moz-focus-inner {
  border: 0;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
input {
  overflow: visible;
}
input[type="text"] {
  border-radius: 0;
}
body {
  max-width: 68rem;
  margin: 0 auto;
  font: 1.6rem/1.25 Arial, sans-serif;
  background-color: #1a1b20;
  color: #4d4d4d;
  font-family: "Open Sans", sans-serif;
}
@media screen and (min-width: 620px) {
  body {
    font-size: 1.9rem;
    line-height: 1.31579;
  }
}
/*END RESETS*/

.exercise_window {
  display: flex;
  flex-direction: row;
  height: 500px;
}
.buttons {
  display: flex;
  position: fixed;
  flex-direction: row;
  bottom: 0;
}
.button {
  font-family: "Open Sans", sans-serif;
  font-weight: 800;
  width: 170px;
  height: 50px;
  background-color: #d0fd3e;
  border-radius: 20px;
  text-align: center;
  color: #000000;
  font-size: 15px;
  padding: 3px;
}
.button:active {
  background-color: rgb(139, 139, 139);
  transform: translateY(2px);
}
.button.next {
  position: fixed;
  right: 0;
}
.exercise_description {
  margin-left: 5px;
  margin-right: 5px;
  min-height: 500px;
  color: #ffffff;
}
.contentEx {
  min-height: 180px;
}
h3 {
  font-size: 20px;
  color: #d0fd3e;
  font-family: "Open Sans", sans-serif;
  font-weight: 800;
}
.selectGroup {
  width: 300px;
  height: 40px;
  font-size: 19px;
  background-color: #d0fd3e;
  color: #000000;
  margin: 0 auto;
  text-align: center;
}
.backToMenu {
  width: 50px;
  height: 50px;
  background-color: #d0fd3e;
  text-align: center;
  color: #000000;
  font-weight: 800;
  font-size: 25px;
  padding: 3px;
}
.backToMenu:active {
  background-color: rgb(139, 139, 139);
  transform: translateY(2px);
}
.selectGroup:active {
  background-color: rgb(139, 139, 139);
  transform: translateY(2px);
}
h2 {
  font-size: 25px;
  text-align: center;
  color: #d0fd3e;
}
.groupList {
  margin-top: 200px;
}
